.newProduct {
    flex: 4;
    margin-left: 100px;
  }
  
  .addProductForm {
    margin-top: 10px;
  }
  
  .addProductItem {
    width: 250px;
    display: flex;
    flex-direction: column;
    margin-bottom: 10px;
  }
  
  .addProductItem > label {
    color: gray;
    font-weight: 600;
    margin-bottom: 10px;
  }
  
  .addProductItem > input {
    padding: 10px;
  }
  
  .addProductItem > select {
    padding: 10px;
  }
  
  .addProductButton {
    margin-top: 10px;
    padding: 7px 10px;
    border: none;
    border-radius: 10px;
    background-color: darkblue;
    color: white;
    font-weight: 600;
    cursor: pointer;
  }

  .center{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin: 1rem;
  }

  .errormessage{
    background-color: hotpink;
    padding: 1rem 3rem;
    margin: 1rem;
    border-radius: 10px;
    color: white
  }

  .successmessage{
    background-color: lawngreen;
    padding: 1rem 3rem;
    margin: 1rem;
    border-radius: 10px;
  }